import 'reflect-metadata'
import React, { useState } from 'react';
import styled from 'styled-components';
import stores from '../stores';
import { List } from 'antd';
import { StoreModel, Task, Duration, TimeLine } from '../model/model'
import DateUtils from '../utils/DateUtils'
import Format from '../utils/Format'

const Div = styled.div`
height: 55vh;
overflow-y: scroll;
white-space: pre-wrap;
background-color:#ffffff;
/* background-color:rgb(227, 233, 231); */

::-webkit-scrollbar {
            -webkit-appearance: none;
            width: 7px;
        }
        ::-webkit-scrollbar-thumb {
            border-radius: 4px;
            background-color: rgba(0, 0, 0, .5);
            box-shadow: 0 0 1px rgba(255, 255, 255, .5);
        }
`

const TimeLineLayout: React.FC = () => {
  const { storeModel } = stores.useStore('mainmodel') as { storeModel: StoreModel };

  // React.useEffect(() => {
  //   console.log("app useEffect")
  // },[])

  function getCurrentDayDoingTasksTimelines(): TimeLine[] {
    return storeModel.getTasks(DateUtils.getMyCurrentDayDur(0))
      .flatMap((task) => task.note.getStartEndWrappedTimeLines(task.doingDurs).map((timeline) => {
        return timeline.getTitled(task.title)
      })).sort((a, b) => {
        if (a.timeStamp > b.timeStamp) {
          return -1
        } else if (a.timeStamp < b.timeStamp) {
          return 1
        } else {
          return 0
        }
      }
      ).filter((timeline) => !timeline.isDivder)
  }

  const NoteItem = styled.div`
    padding-top:1px;
    padding-bottom:1px;
    padding-left:2px;
    padding-right:2px;
    :hover{
    background-color: #a2e3cd;
    };
    background-color:#e3e9e7;
    font-size:11px;
  `

  return (
    <Div>
      <List
        size="small"
        bordered
        dataSource={getCurrentDayDoingTasksTimelines()}
        renderItem={timeline => (
          <NoteItem key={timeline.timeStamp} >
            {/* <span style={{ color: "#ffffff" }}>{Format.formatTimeInSecond2(timeline.timeStamp)}</span>  */}
            <span style={{ color: "#ce9100", paddingRight:"2px"  }}>{Format.formatTimeInSecond2(timeline.timeStamp)}</span> 
            <span style={{ color: "#229a72", display: "inline-block", verticalAlign:"top", maxWidth:"110px", whiteSpace:"nowrap",overflow:"hidden", textOverflow:"ellipsis" }}>  {timeline.title} </span> 
            {timeline.content}
          </NoteItem>
        )}
      />
    </Div>
  );
}

export default TimeLineLayout;
